.background {
    position: fixed;
    background: #444444;
    height: 100vh;
    width: 100vw;
    z-index: -3;
}

.shadow {
    position: fixed;
    height: 100vh;
    width: 100vw;
    background: -webkit-radial-gradient(circle, #00000000, #111111);
    background: -o-radial-gradient(circle, #00000000, #111111);
    background: radial-gradient(circle, #00000000, #111111);
    z-index: -1;
}



